@import "../../../common/style/mixins";

@import "../../../common/style/variables";


.signIn {
  margin: 60px auto;
}

.popupContainer {
    width: 413px;
    height: 100%;
    margin: 0 auto;
    padding: 35px 0 42px 0;

    text-align: center;

    background-color: $background-base-color;
    border-radius: 2px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
}

.title {

  @include headline1();

  margin: 0;
  padding-bottom: 41px;
}

.form {
  display: flex;
  flex-direction: column;

  width: fit-content;
  margin: 0 auto;
}

.inputContainer {
  margin-bottom: 20px;
}

.input {
  width: $popup-input-width;
}

.forgotPassword {

  @include subtitle(500);

  align-self: end;

  color:inherit;
  text-decoration: none;
}

.button {
  width:100%;
  margin: 60px 0 40px 0;
}

.subtitle {

  @include subtitle(600, 0.5);

  margin-bottom: 7px;
}

.loginLink {

  @include title(600);

  color:$primary-color;

  &:hover {
    color:$on-primary-color;
  }
}